.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 50px;
  float: left;
 }
 
 .App-header {
  background-color: #282c34;
  color: white;
  height: 50px;
 }
 
 .App-title {
  font-size: 20px;
  font-weight: 600;
  color: #61dafb;
  float: left;
  padding-top: 10px;
 }
 
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
 
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login-form {
    width: 350px;
    margin : auto;
}

.login-form-button {
    width: 100%;
}
.main {
    margin-top: 40px;
   }
.register {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
   }
.logout {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
}
.main-tabs {
    margin-left: 100px;
    margin-right: 100px;
}
.video-block {
    max-height:400px;
    max-width: 220px;
}
.around-marker-image {
    max-height : 120px;
    max-width: 150px;
}

.around-marker-video {
    max-width: 180px;
    max-height: 300px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

